



































import { Component, Vue } from "vue-property-decorator";
import ExpressWord from "@/components/ExpressWord.vue";
import ExpressDefinition from "@/components/ExpressDefinition.vue";
import AlarmOverlay from "@/components/AlarmOverlay.vue";
import TopBar from "@/components/TopBar.vue";
import BottomBar from "@/components/BottomBar.vue";
import api from "@/data/apis";
import { Onomatopeia } from "@/types/onomatopeia";
import { SquareGrid } from "vue-loading-spinner";
import { Nullable } from "@/types/nullable";
import { Timer } from "@/util/timer";
import { settings } from "@/data/settings";
import { Constants } from "@/constants";
import { sounds } from "@/util/sounds";
import { colors } from "@/data/colors";
import console from "@/util/console";

@Component({
  components: {
    ExpressWord,
    ExpressDefinition,
    SquareGrid,
    AlarmOverlay,
    TopBar,
    BottomBar,
  },
})
export default class Onomatopeias extends Vue {
  currentOnomatopeia: Nullable<Onomatopeia> = null;
  waiting = false;
  remainingTime = 0;
  timer = new Timer(this.onFinish, this.tickCounter);
  hasFinished = false;
  metronomeWorking = false;
  count = 0;
  saveCounter = 0;
  color = "";
  enabledBackButton = false;
  showDefinition = false;

  async mounted(): Promise<void> {
    // this.admobFreeService.prepareAdmobFree();
    // this.admobFreeService.BannerAd();
    // this.ws.reset();
    this.next();
    this.timer.initTimer();
  }

  toogleDefinition(): void {
    console.log("onomatopeia - toogleDefinition");
    this.showDefinition = !this.showDefinition;
  }

  tickCounter(remainingTime: number): void {
    this.remainingTime = remainingTime;
  }

  onFinish(finish: boolean): void {
    if (!finish) {
      return;
    }
    this.executeAtEndAction(finish);
  }

  executeAtEndAction(finish: boolean): void {
    console.log("onomatopeia -  executeAtEndAction", finish);
    console.log("onomatopeia -  executeAtEndAction", settings);
    switch (settings.getInstance().actionAtEnd) {
      case Constants.ActionsAtEnd.NEXT_ITEM: {
        if (settings.getInstance().soundOn) {
          sounds.play(Constants.Sounds.TIMEOUT);
        }
        this.next();
        break;
      }
      case Constants.ActionsAtEnd.ALARM: {
        this.hasFinished = finish;
        if (settings.getInstance().soundOn) {
          sounds.play(Constants.Sounds.TIMEOUT);
        }
        break;
      }
      default: {
        break;
      }
    }
  }

  async next(): Promise<void> {
    if (this.count > settings.getInstance().countsBeforeAds) {
      // this.admobFreeService.InterstitialAd();
      this.count = 0;
    }
    this.count++;
    this.hasFinished = false;
    this.waiting = true;
    const data = await api.randomOnomatopeia();
    this.currentOnomatopeia = data.body;
    this.waiting = false;
    if (settings.getInstance().changeColorOn) {
      this.color = colors.next();
    }
    this.enabledBackButton = true;
    this.timer.resetTimer();
  }

  async before(): Promise<void> {
    // TODO change this to get the last word
    const data = await api.randomOnomatopeia();
    this.currentOnomatopeia = data.body;
    if (settings.getInstance().changeColorOn) {
      this.color = colors.before();
    }
    this.enabledBackButton = false;
  }
}
